<template>
  <v-card tile color="grey lighten-2" height="100vh">
    <v-card-text>
      <!-- <v-container fluid> -->
      <v-row no-gutters class="mt-5" justify="center" align="center">
        <v-col class="hidden-sm-and-down" sm="4" md="5">
          <v-card
            tile
            elevation="8"
            height="500"
            class="d-flex px-4 flex-column align-center"
            color="#253c88"
          >
            <v-img
              contain
              :src="require('@/assets/eduRoom-white2.png')"
            ></v-img>
          </v-card>
        </v-col>
        <v-col xs="12" sm="8" md="5">
          <v-card
            elevation="8"
            tile
            :loading="loading"
            height="500"
            color="base"
          >
            <v-card-title>
              <h3>Login here !</h3>
            </v-card-title>
            <v-card-subtitle>
              Welcome, please login to your account
            </v-card-subtitle>
            <v-form v-model="valid" ref="myForm" @submit.prevent="login">
              <v-container class="px-7">
                <v-text-field
                  filled
                  dense
                  outlined
                  :rules="[rules.required, rules.email]"
                  v-model="form.email"
                  autocomplete="email"
                  label="Email"
                  prepend-inner-icon="mdi-email"
                ></v-text-field>
                <v-text-field
                  dense
                  filled
                  outlined
                  :rules="[rules.required]"
                  v-model="form.password"
                  type="password"
                  autocomplete="current-password"
                  label="Password"
                  prepend-inner-icon="mdi-lock"
                >
                </v-text-field>
                <div class="d-flex justify-space-between mb-5">
                  <v-btn color="primary" outlined to="/register"
                    >Register</v-btn
                  >
                  <v-btn tile color="primary" type="submit">Login</v-btn>
                </div>
                <div class="or__divider">
                  <v-divider />
                  <span>OR</span>
                </div>
                <div class="mt-6">
                  <v-btn to="/artikel" block class="primary white--text">
                    <v-icon left>mdi-newspaper</v-icon>
                    Lihat Artikel
                  </v-btn>
                </div>
                <div class="mt-6">
                  <v-btn to="/proposal" block class="error white--text">
                    <v-icon color="white" left>mdi-file-pdf</v-icon>
                    Lihat Proposal
                  </v-btn>
                </div>
                <div class="mt-6">
                  <v-btn
                    block
                    outlined
                    class="green white--text"
                    @click.stop="dialog = true"
                  >
                    Lihat akun admin
                    <br />
                    (Testing purpose only)
                  </v-btn>
                </div>
                <v-dialog v-model="dialog" max-width="290">
                  <v-card>
                    <v-card-title class="headline"> ADMIN: </v-card-title>

                    <v-card-text>
                      <ul>
                        <li>Email:admin@gmail.com</li>
                        <li>Password:123456</li>
                      </ul>
                    </v-card-text>

                    <v-card-actions>
                      <v-spacer></v-spacer>

                      <v-btn
                        color="error darken-1"
                        text
                        @click="dialog = false"
                      >
                        exit
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-container>
            </v-form>
          </v-card>
        </v-col>
      </v-row>
      <!-- </v-container> -->
    </v-card-text>
  </v-card>
</template>

<script>
import { reactive, ref } from "@vue/composition-api";
export default {
  setup(_, { root }) {
    const dialog = ref(false);
    const rules = reactive({
      required: (value) => !!value || "Data harus diisi.",
      minLength: (value) =>
        value.length >= 6 || "Tidak boleh kurang dari 6 huruf",
      email: (value) => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return pattern.test(value) || "Email tidak valid.";
      },
    });
    const valid = ref(false);
    const myForm = ref(null);
    const loading = ref(false);
    const form = reactive({
      email: "",
      password: "",
    });
    const login = () => {
      if (valid.value) {
        loading.value = "primary";
        root.axios
          .post("auth/login", form, {
            headers: {
              "Content-Type": "application/json",
            },
          })
          .then((res) => {
            localStorage.token = res.data.token;
            root.$router.push("/home");
            root.bus.$emit("showSnackbar", {
              message: "Berhasil Login",
              show: true,
            });
          })
          .catch((err) => {
            root.bus.$emit("showSnackbar", {
              message: "Login gagal, username/password salah",
              show: true,
              color: "red",
            });

            console.log(err.response);
          })
          .then(() => {
            loading.value = false;
            form.password = "";
          });
      } else {
        myForm.value.validate();
      }
    };
    return { loading, form, myForm, dialog, login, valid, rules };
  },
};
</script>

